$button-padding: $padding !default;
$button-font-size: 1em !default;
$button-line-height: 1.5em !default;
$button-inner-spacing: 0.5em !default;
$button-background: rgba($black, 0.05) !default;
$button-background-hover: rgba($black, 0.1) !default;
$button-border-width: 1px !default;
$button-border-color: transparent !default;
$button-border-color-hover: transparent !default;
$button-border-radius: $border-radius !default;

$button_size_small-padding: $padding-small !default;
$button_size_small-font-size: $font-size-small !default;
$button_size_large-padding: $padding-large !default;
$button_size_large-font-size: $font-size-large !default;

$button_min-width: 110px !default;

$button_outline_border-width: 1px !default;
